import React, { Component } from 'react';
import {connect} from 'react-redux';

import {DragDropContext} from 'react-beautiful-dnd';

import EffectContainer from './containers/EffectContainer';
import ControlsContainer from './containers/ControlsContainer';
import {moveSound, removeSoundReference, addSoundReference} from './actions/effectsActions';
import {changeSoundVar} from './actions/soundsActions';

class App extends Component {
  constructor(props){
    super(props);
    this.onDragEnd = this.onDragEnd.bind(this);
    this.getPlayPause = this.getPlayPause.bind(this);
    this.removePlayPause = this.removePlayPause.bind(this);
    this.state = {
      playPauseButtons: {}
    }
  }

  onDragEnd(result){
    const {destination, source, draggableId} = result;
    if(destination === null){return;}
    if(destination.droppableId === source.droppableId){
      this.props.moveSound(destination.droppableId, draggableId, source.index, destination.index);
      return;
    }

    this.props.displaceSound(source.droppableId, destination.droppableId, draggableId, destination.index);
  }

  getPlayPause(play, pause, id){
    const ref = {[id]:{
      "play":play,
      "pause":pause
    }}
    this.setState(prevState =>({
      playPauseButtons: Object.assign({}, prevState.playPauseButtons,ref)
    }));
  }

  removePlayPause(id){
    let playPauseButtons = Object.assign({},this.state.playPauseButtons);
    delete playPauseButtons[id];
    console.log(playPauseButtons);
    this.setState({
      playPauseButtons: playPauseButtons
    });
  }

  render() {
    return (
      <div className="App container-fluid">
        <div className="row justify-content-between">
          <div className="col-12 col-md-6 col-lg-4 col-xl-2">
            <ControlsContainer playPause={this.state.playPauseButtons}/>
          </div>
          <div className="col-12 col-md-6 col-lg-8 col-xl-10">
            <DragDropContext onDragEnd={this.onDragEnd}>
              <EffectContainer getPlayPause={this.getPlayPause} removePlayPause={this.removePlayPause}/>
            </DragDropContext>
          </div>
          <div className="col-2"></div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) =>{
  return{
    effects: state.effects,
    sounds: state.sounds
  }
}

const mapDispatchToProps = (dispatch) =>{
  return{
    moveSound: (effectId, soundId, oldIndex, newIndex) =>{
      dispatch(moveSound(effectId, soundId, oldIndex, newIndex));
    },
    displaceSound: (oldEffectId, newEffectId, soundId, index) =>{
      dispatch((dispatch)=>{
        dispatch(removeSoundReference(oldEffectId, soundId));
        dispatch(addSoundReference(newEffectId, soundId, index));
        dispatch(changeSoundVar(soundId,'effectId', newEffectId));
      });
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(App);



